  methods: {
    search() {
      if (!this.searchQuery.trim()) {
        alert('ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÖ≥ÈîÆËØç');
        return;
      }

      // ÁÆÄÂçïÁöÑÂÖ≥ÈîÆËØçÂåπÈÖç
      const query = this.searchQuery.trim();
      let found = false;
      
      for (const garbage of this.garbageDatabase) {
        if (garbage.name.includes(query)) {
          this.recognitionResult = garbage;
          found = true;
          break;
        }
      }
      
      if (!found) {
        alert(`Êú™ÊâæÂà∞"${query}"ÁöÑÂûÉÂúæÂàÜÁ±ª‰ø°ÊÅØÔºåËØ∑Â∞ùËØïÂÖ∂‰ªñÂÖ≥ÈîÆËØç`);
