    },
    openCamera() {
      this.showCameraModal = true;
      
      setTimeout(() => {
        const video = document.getElementById('videoElement');
        
        // ËØ∑Ê±ÇÊëÑÂÉèÂ§¥ÊùÉÈôê
        navigator.mediaDevices.getUserMedia({ video: { facingMode: 'environment' } })
          .then(stream => {
            this.mediaStream = stream;
            video.srcObject = stream;
            video.play();
          })
          .catch(error => {
            console.error('Êó†Ê≥ïËÆøÈóÆÊëÑÂÉèÂ§¥:', error);
            alert('Êó†Ê≥ïËÆøÈóÆÊëÑÂÉèÂ§¥ÔºåËØ∑Ê£ÄÊü•ÊùÉÈôêËÆæÁΩÆ');
            this.closeCamera();
          })
      }, 300);
    },
    closeCamera() {
      this.showCameraModal = false;
      this.capturedImage = null;
      
      // ÂÅúÊ≠¢ÊëÑÂÉèÂ§¥
      if (this.mediaStream) {
        this.mediaStream.getTracks().forEach(track => track.stop());
        this.mediaStream = null;
      }
    },
    capturePhoto() {
      const video = document.getElementById('videoElement');
      const canvas = document.getElementById('canvasElement');
      const context = canvas.getContext('2d');
      
      // ËÆæÁΩÆcanvasÂ§ßÂ∞è‰∏éËßÜÈ¢ë‰∏ÄËá¥
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      
      // ÁªòÂà∂ÂΩìÂâçËßÜÈ¢ëÂ∏ßÂà∞canvas
      context.drawImage(video, 0, 0, canvas.width, canvas.height);
      
      // Â∞ÜcanvasËΩ¨Êç¢‰∏∫ÂõæÁâáURL
      this.capturedImage = canvas.toDataURL('image/jpeg');
    },
    analyzePhoto() {
      // Ê®°ÊãüÂûÉÂúæËØÜÂà´ËøáÁ®ã
      alert('Ê≠£Âú®ÂàÜÊûêÁÖßÁâá...');
      
      // Ê®°ÊãüËØÜÂà´ÁªìÊûú - ÈöèÊú∫ÈÄâÊã©‰∏Ä‰∏™ÂûÉÂúæ
      setTimeout(() => {
        const randomIndex = Math.floor(Math.random() * this.garbageDatabase.length);
        this.recognitionResult = this.garbageDatabase[randomIndex];
        this.closeCamera();
      }, 1500);
    },
    // ËØ≠Èü≥ËØÜÂà´ÂäüËÉΩ
    startVoiceRecognition() {
      // Ê£ÄÊü•ÊµèËßàÂô®ÊòØÂê¶ÊîØÊåÅËØ≠Èü≥ËØÜÂà´
      if (!('webkitSpeechRecognition' in window || 'SpeechRecognition' in window)) {
        alert('ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅËØ≠Èü≥ËØÜÂà´ÂäüËÉΩÔºåËØ∑‰ΩøÁî®ChromeÊµèËßàÂô®');
        return;
      }
      
      // ÂàõÂª∫ËØ≠Èü≥ËØÜÂà´ÂÆû‰æã
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
      this.voiceRecognition = new SpeechRecognition();
      
      // ËÆæÁΩÆËØÜÂà´ÂèÇÊï∞
      this.voiceRecognition.continuous = false;
      this.voiceRecognition.interimResults = false;
      this.voiceRecognition.lang = 'zh-CN';
      
      // ËØÜÂà´ÂºÄÂßã‰∫ã‰ª∂
      this.voiceRecognition.onstart = () => {
        this.isListening = true;
        this.recognitionResult = null;
      };
      
      // ËØÜÂà´ÁªìÊûú‰∫ã‰ª∂
      this.voiceRecognition.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
        this.isListening = false;
        
        // Ê®°ÊãüÂûÉÂúæËØÜÂà´
        this.recognizeGarbage(transcript);
      };
      
      // ËØÜÂà´ÈîôËØØ‰∫ã‰ª∂
      this.voiceRecognition.onerror = (event) => {
        console.error('ËØ≠Èü≥ËØÜÂà´ÈîôËØØ:', event.error);
        this.isListening = false;
        alert('ËØ≠Èü≥ËØÜÂà´Â§±Ë¥•ÔºåËØ∑ÈáçËØï');
      };
      
      // ÂºÄÂßãËØÜÂà´
      this.voiceRecognition.start();
    },
    recognizeGarbage(text) {
      // ÁÆÄÂçïÁöÑÂÖ≥ÈîÆËØçÂåπÈÖç
      let recognized = false;
      for (const garbage of this.garbageDatabase) {
        if (text.includes(garbage.name)) {
          this.recognitionResult = garbage;
          recognized = true;
          break;
        }
      }
      
      if (!recognized) {
        alert(`Êú™ÊâæÂà∞"${text}"ÁöÑÂûÉÂúæÂàÜÁ±ª‰ø°ÊÅØÔºåËØ∑Â∞ùËØïÂÖ∂‰ªñÂÖ≥ÈîÆËØç`);
      }
    },
    navigateTo(feature) {
      if (feature === 'guide') {
        this.showClassificationGuide = true;
      } else if (feature === 'exchange') {
        this.showPointsMall = true;
      } else {
        alert(`ÂØºËà™Âà∞${feature}ÂäüËÉΩÈ°µÈù¢`);
      }
    },
    
    // Ê∑ªÂä†ÔºöÂ§ÑÁêÜÁßØÂàÜÂÖëÊç¢ÁöÑÊñπÊ≥ï
    handleExchange(productId, points) {
      this.userPoints -= points;
      alert(`ÂÖëÊç¢ÊàêÂäüÔºÅÂ∑≤Êâ£Èô§${points}ÁßØÂàÜ`);
    }
  }
